:root {
  --clr-primary: #fffefe;
  --clr-secondary: #fbb03b;
  --clr-accent: #fece00;
  --bg-primary: #5268e7;
  --bg-secondary: #3e54d2;
  --clr-success: #1cb142;
  --clr-warning: #fbb03b;
  --clr-danger: #f42850;
}

* {
  margin: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Nunito', sans-serif;
  color: #fffefe;
  color: var(--clr-primary);
  background-color: #5268e7;
  background-color: var(--bg-primary);
}

.container {
  max-width: 85%;
  margin: 0 auto;
  padding: 0.5em;
}

/*
  Header
*/

.nav-bar {
  display: flex;
  margin-top: 1em;
  justify-content: space-between;
}

.logo {
  font-weight: 800;
  text-transform: uppercase;
  margin: 0;
}

.logo-accent {
  color: #fece00;
  color: var(--clr-accent);
}

.nav-list {
  display: flex;
  padding: 0;
}

.nav-item {
  list-style: none;
  padding-left: 0.6em;
}

.nav-link {
  font-weight: bold;
  text-decoration: none;
  color: #fffefe;
  color: var(--clr-primary);
}

/*
  Hero
*/

.hero-content {
  text-align: center;
}

.hero-title {
  margin-top: 1.2em;
  margin-bottom: 0;
  font-size: 2.2rem;
  font-weight: 800;
}

.hero-text {
  margin-top: 1em;
}

.hero-link {
  display: inline-block;
  color: #fbb03b;
  color: var(--clr-secondary);
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  margin-top: 1em;
}

.stats {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1em;
}

.card {
  width: 240px;
  height: 180px;
  margin-top: 2em;
  padding: 1em;
  text-align: center;
  color: #fff;
  border: 5px solid rgba(255, 255, 255, 0.2767);
  border-radius: 12px;
  box-shadow: 0px 8px 25px #4056d4;
}

.card-info {
  font-size: 2rem;
  font-weight: 800;
  margin-top: 1em;
}

.card-title {
  font-size: 1.2rem;
  font-style: italic;
  font-weight: 800;
  margin-top: 0.3em;
}

.card-danger {
  background-color: #f42850;
  background-color: var(--clr-danger);
}

.card-warning {
  background-color: #fbb03b;
  background-color: var(--clr-warning);
}

.card-success {
  background-color: #1cb142;
  background-color: var(--clr-success);
}

/*
  Prevention
*/

#prevention {
  background-color: #3e54d2;
  background-color: var(--bg-secondary);
}

.section-title {
  font-size: 2rem;
  font-weight: 800;
  margin: 0;
}

.stickers {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1em;
}

.sticker {
  text-align: center;
  padding: 1em;
}

.sticker-title {
  font-size: 1.2rem;
}

.sticker-img {
  margin-top: 0.5em;
}

/*
  Symptoms
*/

.symptoms-title {
  font-size: 2rem;
  font-weight: 800;
  text-align: right;
  margin: 0;
}

.symptoms-flex {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1em;
}

.symptoms-list,
.symptoms-box {
  padding-top: 1em;
}

.symptoms-list {
  width: 200px;
  list-style: none;
  padding-left: 0;
}

.symptoms-list-item {
  font-size: 1.2rem;
  font-weight: 800;
  text-align: center;
  padding: 0.5em;
  margin: 0.5em;
  background: #6075eb;
  border: 3px solid #7087ff;
  box-sizing: border-box;
  border-radius: 8px;
}

.symptoms-img {
  width: 100%;
}

/*
  Footer
*/

footer {
  background-color: #3e54d2;
  background-color: var(--bg-secondary);
}

.footer-flex {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.col {
  margin-top: 0.5em;
}

.footer-logo {
  font-size: 0.8rem;
  font-weight: 800;
  text-transform: uppercase;
  margin: 0;
}

/* .footer-text {
  font-size: 1rem;
} */

.footer-link {
  color: inherit;
  text-decoration: none;
}

.footer-link:hover {
  text-decoration: underline;
}

